<template>
 <el-container>
  <el-main>
   <el-form :inline="true" class="demo-form-inline">
    <el-form-item label="学生学号">
        <el-input placeholder="学生学号" v-model="studentid"></el-input>
    </el-form-item>
    <el-form-item label="学期">
      <el-select
        placeholder="选择学期"
        clearable
        v-model="select"
      >
        <el-option label="2024-2025春季学期" value="2024-2025春季学期" />
        <el-option label="2024-2025秋季学期" value="2024-2025秋季学期" />
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="query">查询</el-button>
    </el-form-item>
  </el-form>
<!-- 表格 -->
 <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="course"
      label="课程"
      width="180">
    </el-table-column>
    <el-table-column
      prop="score"
      label="成绩"
      width="180">
    </el-table-column>
    <el-table-column
      prop="xuefen"
      label="学分">
    </el-table-column>
  </el-table>
  </el-main>
  </el-container>
  
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters(['getUsername']),
    username() {
      return this.getUsername;  // 直接访问 getter 属性
    }
  },
  data() {
    return {
      select: null,
      tableData: [],
      studentid:''
    };
  },
  methods: {
    query() {
      let params = new URLSearchParams();
      params.append('username', this.studentid);
      params.append('term', this.select);
      
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8688/queryscore",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        this.tableData = result.data.map(item => ({
          score: item[0],
          teacher: item[1],
          course: item[2],
          xuefen: item[3]
        }));
        this.$message({
          type: 'success',
          message: '查询成功!'
        });
      }).catch((error) => {
        console.error('请求失败:', error);
        this.tableData = []; // 清空表格内容
        this.$message({
          type: 'warning',
          message: '查询失败!'
        });
      });
    }
  },
};
</script>

<style scoped>
.demo-form-inline .el-input {
  --el-input-width: 200px;
}

.demo-form-inline .el-select {
  --el-select-width: 200px;
}
</style>
